.sprite:before,
.sprite__chat:before,
.sprite__phone:before {
  content: ' ';
  background-image: url("../images/sprite/svg-sprite.svg");
  background-repeat: no-repeat;
  background-size: 2.375em 1.25em;
  display: inline-block;
}
.no-svg .sprite:before,
.no-svg .sprite__chat:before,
.no-svg .sprite__phone:before {
  background-image: url("../images/sprite/svg-sprite.png");
}
.sprite__chat:before {
  background-position: 0em 0em;
  width: 1.25em;
  height: 1.25em;
}
.sprite__phone:before {
  background-position: -1.25em 0em;
  width: 1.125em;
  height: 1.125em;
}
body {
  margin: 0;
  padding: 0;
}
